.our-expertise {
  background-color: $background-white;
  border-radius: 30px;

  &__container {
    margin: 120px 0 0;
    padding: 72px 0;

    overflow: hidden;
    
    @include onTablet {
      margin: 148px 0 0;
      padding: 128px 0;
    }
  }

  &__content {

    @include onSmallDesktop {
      display: flex;
      gap: 137px;
    }
  
    @include onDesktop {
      gap: 177px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 44px;
    line-height: 120%;
    text-align: center;
    word-wrap: break-word;
    
    color: $color-title;
    
    @include onTablet {
      font-size: 52px;
      line-height: 150%;
    }
  }

  &__card {
    margin: 56px 0 0;
    
    @include onTablet {
      margin: 64px 0 0;
    }
  }
}
